import { mapState } from "vuex";

export default {
  name: "ProductsListFilter",

  computed: {
    ...mapState({
      categories: (state) => state.filters.categories,
    }),
  },
  methods: {
    onclick() {
      this.$emit('close')
    },
  },
  created() {
    if (!this.categories) {
      this.$store.dispatch("filters/fetchCategories");
    }
  },
};
